html.is-overflow-hidden {
	overflow-y: scroll; }


body {
	@include fap;
	@include f(14px,400,2);
	color: #46362E;
	@include font-smoothing;
	min-width: 320px;
	background: #FFF;
	@include fadeout(.6s);
	&.is-main {
		overflow: hidden;
		@include md {
			overflow: auto; } }
	&.is-loaded {
		@include fadein; }
	.is-open-menu &,
	.is-overflow-hidden & {
		position: fixed;
		width: 100%; }
	@include md {
		font-size: 12px;
		line-height: 1.82; } }

.title-lg {
	@include ffo;
	@include f(72px,400,1.29);
	@include css-lock(2,6,375,1440,'letter-spacing');
	@include md {
		@include css-lock(26,72,375,1023); }
	span {
		display: block; } }
.title {
	@include ffo;
	@include f(60px,400,1);
	@include css-lock(2,6,375,1440,'letter-spacing');
	@include md {
		@include css-lock(26,60,375,1023); }
	span {
		display: block; } }
.btn {
	padding: 23px 40px;
	color: #fff;
	@include fap;
	@include f(14px,700,1);
	vertical-align: top;
	background-color: #DA4A48;
	transition: background-color .3s;
	display: inline-block;
	@include md {
		padding: 19px 27px;
		font-size: 12px; }
	@include hover {
		background-color: darken(#DA4A48, 15%); } }
