.topic {
	position: relative;
	padding: 80px;
	min-height: 350px;
	align-items: center;
	justify-content: center;
	color: #fff;
	display: flex;
	@include sm {
		padding: 30px;
		min-height: 150px; }
	&__img {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: #46362E;
		&:before {
			position: absolute;
			content: '';
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			z-index: 1;
			background-image: url("data:image/svg+xml,%3Csvg width='1128' height='740' viewBox='0 0 1128 740' xmlns='http://www.w3.org/2000/svg'%3E%3Ctitle%3E Lines %3C/title%3E%3Cg fill='%23FFF' fill-rule='evenodd' opacity='.1'%3E%3Cpath d='M644 0h1v740h-1z'/%3E%3Cpath d='M805 0h1v740h-1z'/%3E%3Cpath d='M966 0h1v740h-1z'/%3E%3Cpath d='M1127 0h1v740h-1z'/%3E%3Cpath d='M483 0h1v740h-1z'/%3E%3Cpath d='M322 0h1v740h-1z'/%3E%3Cpath d='M161 0h1v740h-1z'/%3E%3Cpath d='M0 0h1v740H0z'/%3E%3C/g%3E%3C/svg%3E%0A");
			background-repeat: no-repeat;
			background-position: center;
			@include md {
				display: none; } }
		img {
			position: relative;
			opacity: .8;
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: center;
			display: block; } }
	&__inner {
		position: relative;
		z-index: 1; } }
