// GENERATE GRID EXAMPLE
// CUSTOM PADDING ON BREAKPOINTS - NOT INCLUDED IN MIXIN
@include generate_grid($columns: 12, $sizeXs: true, $sizeSm: true, $sizeMd: true, $sizeLg: true, $padding: 15px);

.out {
	position: relative;
	padding: 0 80px 80px;
	min-height: calc(100vh - 80px);
	overflow: hidden;
	@include md {
		padding: 0 20px 52px;
		min-height: calc(100vh - 52px); } }

.inner {
	margin: 0 auto;
	max-width: 943px; }

.wrap {
	padding: 120px 0 80px;
	@include md {
		padding: 31px 0 28px; } }
